import { defineComponent, computed, ref, nextTick } from 'vue';
import { useStore } from '@/store';
import pageContent from '@/components/page-content/index';
import pageSearch from '@/components/page-search/index';
import pageModal from '@/components/page-modale/index';
import { ContentToTable } from './config/content.config';
import { FormConfig } from './config/search.config';
import { ModalConfig } from './config/modal.config';
import { usePageSearch } from '@/hooks/usePageSearch';
import { usePageModal } from '@/hooks/usePageModal';
import { getMenuLeafKeys } from '@/utils/map-menus';
export default defineComponent({
  setup() {
    //
    const ElTreeRef = ref();

    const eidtCbk = item => {
      const leftkeys = getMenuLeafKeys(item.menuList);
      nextTick(() => {
        ElTreeRef.value?.setCheckedKeys(leftkeys, false);
      });
    };

    const [PageContentRef, handleQueryClick, handleResetClick] = usePageSearch();
    const [pageModalRef, defaultInfo, handleNewData, handleEditData] = usePageModal(undefined, eidtCbk);
    const store = useStore(); //storemenus
    //setupcomputed

    const menus = computed(() => store.state.entireMenu); //

    const otherInfo = ref({});

    const handleCheckChange = (data1, data2) => {
      const checkId = data2.checkedKeys;
      const halfcheckId = data2.halfCheckedKeys;
      const menuList = [...checkId, ...halfcheckId];
      console.log(menuList);
      otherInfo.value = {
        menuList
      };
    };

    return {
      ContentToTable,
      FormConfig,
      handleQueryClick,
      handleResetClick,
      PageContentRef,
      ModalConfig,
      pageModalRef,
      defaultInfo,
      handleNewData,
      handleEditData,
      menus,
      handleCheckChange,
      otherInfo,
      ElTreeRef
    };
  },

  components: {
    pageContent,
    pageSearch,
    pageModal
  }
});